.acount-view {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  &__photo {
    border-radius: 50%;
    width: 30%;
    margin-bottom: 3rem;
  }
  &__detail {
    margin-bottom: 2rem;
    h3 {
      font-size: 2rem;
      color: $color-primary;
      text-transform: uppercase;
    }
  }
}
